.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.header{
  background-color: gray;
}
.balance{
  background-color: lightgray;
}
.main{
  /* height: 350px; */
  background-color: lightgray;
}

.main >col{
  padding: 15px;
  margin: 20px;
}

.trans{
    padding: 10px;
    margin: 10px;
    margin-left: 200px;
    background-color: gray;
    width: 300px;
    height: 200px;
    overflow: scroll;
    color: white;
}
.far fa-trash-alt{
  width: 10px;
  height: 5px;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
